import React, { Component } from "react";
import "./App.css";
import Button from "react-bootstrap/lib/Button";
import ButtonToolbar from "react-bootstrap/lib/ButtonToolbar";
import MenuItem from "react-bootstrap/lib/MenuItem";
import DropdownButton from "react-bootstrap/lib/DropdownButton";

class Buttons extends Component {
  
  handleSelect = (evt) => {
    this.props.gridSize(evt);
  }

  render() {
    


    return (

      <div className="ButtonContainer">
        <ButtonToolbar>
          <Button bsStyle="default" onClick={this.props.playButton}>
            Play
          </Button>
          <Button bsStyle="default" onClick={this.props.pauseButton}>
            Pause
          </Button>
          <Button bsStyle="default" onClick={this.props.clear}>
            Clear
          </Button>
          <Button bsStyle="default" onClick={this.props.slow}>
            Slow
          </Button>
          <Button bsStyle="default" onClick={this.props.fast}>
            Fast
          </Button>
          <Button bsStyle="default" onClick={this.props.seed}>
            Seed
          </Button>
          <DropdownButton
            title="Grid Size"
            id="size-menu"
            onSelect={this.handleSelect}
          >
            <MenuItem eventKey="1">20x10</MenuItem>
            <MenuItem eventKey="2">50x30</MenuItem>
            <MenuItem eventKey="3">70x50</MenuItem>
          </DropdownButton>
        </ButtonToolbar>
      </div>

    );
  
  }

}

export default Buttons;


