import React, { Component } from 'react';
import './App.css';
import Box from "./Box"

class Grid extends Component {


  render() {

  	const width = this.props.cols * 15;
  	var rowsArr = [];

  	var boxClass = "";
  	for (var i = 0; i < this.props.rows; i++) {
  		for (var y = 0; y < this.props.cols; y++) {
  			let boxId = i+"_"+y;
  			boxClass = this.props.gridFull[i][y] ? "BoxOn" : "BoxOff";
  			rowsArr.push(
  				<Box
  					boxClass={boxClass}
  					key={boxId}
  					boxId={boxId}
  					row={i}
  					col={y}
  					selectBox={this.props.selectBox}
  				/>
  				)
  		}
  	}

    return (

    	<div className="Grid" style={{width:width}}>
    		{rowsArr}
    	</div>

    );

  }

}

export default Grid;



