import React, { Component } from 'react';
import './App.css';
import Button from "react-bootstrap/lib/Button";
import Panel from "react-bootstrap/lib/Panel";
import Grid from './Grid';
import Buttons from './Buttons';

class App extends Component {

  constructor() {
    super();
    this.speed = 100;
    this.cols = 50;
    this.rows = 30;
    this.state = {
      open: false,
      generation: 0,
      gridFull: Array(this.rows).fill().map(() => Array(this.cols).fill(false))
    }
  }

  selectBox = (row, col) => {
    let gridCopy = arrayClone(this.state.gridFull);
    gridCopy[row][col] = !gridCopy[row][col];
    this.setState({
      gridFull: gridCopy
    })
  }

  seed = () => {
    let gridCopy = arrayClone(this.state.gridFull);  
    for (let i = 0 ; i < this.rows; i++) {
      for (let y = 0; y < this.cols; y++) {
        if (Math.floor(Math.random() * 4) === 1) {
            gridCopy[i][y] = true;
        }
      }
    }
    this.setState({
      gridFull: gridCopy
    })
  }

  playButton = () => {
    clearInterval(this.intervalId);
    this.intervalId = setInterval(this.play, this.speed)
  }

  pauseButton = () => {
    clearInterval(this.intervalId);
  }

  slow = () => {
    this.speed = 1000;
    this.playButton();
  }

  fast = () => {
    this.speed = 100;
    this.playButton();
  }

  clear = () => {
    var grid = Array(this.rows).fill().map(() => Array(this.cols).fill(false));
    this.setState({gridFull : grid, generation: 0})
  }

  gridSize = (size) => {
    switch (size) {
      case "1": this.cols = 20; this.rows = 10; break;
      case "2": this.cols = 50; this.rows = 30; break;
      default: this.cols = 70; this.rows = 50;
    }
    this.clear();
  }

  play = () => {
    let g = this.state.gridFull;
    let g2 = arrayClone(this.state.gridFull);

    for (let i = 0; i < this.rows ; i++) {
      for (let y = 0; y < this.cols; y++) {
        let count = 0;
          if (i>0 && y>0) if (g[i-1][y-1]) count++;
          if (i>0) if (g[i-1][y]) count++;
          if (i>0 && y<this.rows-1) if (g[i-1][y+1])count++;
          if (y>0) if (g[i][y-1]) count++; 
          if (y<this.cols-1) if (g[i][y+1]) count++;
          if (i<this.rows-1 && y>0) if (g[i+1][y-1]) count++;
          if (i<this.rows-1) if (g[i+1][y]) count++;
          if (i<this.rows-1 && y<this.cols-1) if (g[i+1][y+1]) count++;
          if (g[i][y] && (count < 2 || count > 3))  g2[i][y] = false;
          if (!g[i][y] && count === 3) g2[i][y] = true;
      }
    }

    this.setState({
      gridFull: g2,
      generation: this.state.generation+1
    });

  }

  componentDidMount() {
    this.seed();
    this.playButton();
  }

  render() {
   
    return (

      <div>
            
            <div class="logo">
            <h1>Game of Life</h1>
            <h5>originally created by J.H. Conway</h5>
                        <Button className="Button" onClick={() => this.setState({ open: !this.state.open })}>
            Click to read instructions
            </Button>
            </div>

            <Panel className="Panel" id="collapsible-panel-example-1" expanded={this.state.open}>
              <Panel.Collapse className="PanelInside">
                
                 &emsp;The Game of Life is a zero-player game, meaning that its evolution is determined by its initial state, requiring no further input.
                 One interacts with the Game of Life by creating an initial configuration and observing how it evolves<br/><br/>
                 &emsp;It's an orthogonal grid of square cells, each of which is in one of two possible states, <b>alive</b> or <b>dead</b>, or "populated" or "unpopulated". Every cell interacts with its eight neighbours, which are the cells that are horizontally, vertically, or diagonally adjacent. At each step in time, the following transitions occur:<br/><br/>
                 <ol>
                  <li>&nbsp;Any live cell with fewer than two live neighbours dies, as if caused by underpopulation.</li>
                  <li>&nbsp;Any live cell with two or three live neighbours lives on to the next generation.</li>
                  <li>&nbsp;Any live cell with more than three live neighbours dies, as if by overpopulation.</li>
                  <li>&nbsp;Any dead cell with exactly three live neighbours becomes a live cell, as if by reproduction.</li>
                 </ol>
              </Panel.Collapse>
            </Panel>
            
      <div className="mainContainer container">
        <div className="row">
          <div className="col-md-12">
            
            
              <Buttons 
                playButton={this.playButton}
                pauseButton={this.pauseButton}
                slow={this.slow}
                fast={this.fast}
                clear={this.clear}
                seed={this.seed}
                gridSize={this.gridSize}
              />
          
            
            <Grid 
              gridFull={this.state.gridFull}
              rows={this.rows}
              cols={this.cols}
              selectBox={this.selectBox}
            />
            <div className="generationContainer">Generation: {this.state.generation}</div>
            <div className="copyright">coded by Rafael Pessoa</div>
          </div>
        </div>
      </div>

      </div>  
    );

  }
}

function arrayClone(arr) {
  return JSON.parse(JSON.stringify(arr));
}

export default App;



